.gallery {
  background-image: linear-gradient(to bottom, $background-color 34%, #fff 34%);
  @include tablet {
    background-image: linear-gradient(
      to bottom,
      $background-color 55%,
      #fff 55%
    );
  }
  @include desktop {
    background-image: linear-gradient(
      to bottom,
      $background-color 27%,
      #fff 27%
    );
  }
}

.gallery .container {
  position: relative;
  min-height: 136px;

  @media screen and(min-width: 320px) and (max-width: 479px) {
    max-width: 320px;
  }
  @include mobile {
    min-height: 202px;
  }
  @include tablet {
    min-height: 327px;
  }
  @include desktop {
    min-height: 430px;
  }
}

.gallery img {
  position: absolute;
  border-radius: 8px;

  &:not(:nth-child(5)) {
    width: 43px;
    @include mobile {
      width: 65px;
    }
    @include tablet {
      width: 105px;
    }
    @include desktop {
      width: 138px;
    }
  }
  &:nth-child(5) {
    width: 89px;
    @include mobile {
      width: 134px;
    }
    @include tablet {
      width: 214px;
    }
    @include desktop {
      width: 283px;
    }
  }
  @include tablet {
    border-radius: 20px;
  }
  &:hover {
    transform: scale(1.5);
    z-index: 9;
  }
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

